.newsletter h1 {
  font-family: 'Oswald', 'Open Sans', sans-serif;
  font-weight: 400; }

.newsletter .widget .title {
  font-size: 16px; }

.newsletter .widget form input {
  font-size: 16px;
  background-color: #f1f1f1;
  color: #333; }

.newsletter .widget form button {
  font-size: 16px;
  background: #f00;
  color: #fff; }

.newsletter-form {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: stretch;
  flex-wrap: wrap;
  margin: 48px auto; }
  .newsletter-form * {
    box-sizing: border-box; }
  .newsletter-form-col.fields, .newsletter-form-col.interests {
    max-width: 500px;
    width: 100%; }
  .newsletter-form-col.break {
    width: 1px;
    background-color: #999; }
  .newsletter-form-col.button {
    margin-top: 48px;
    flex: 2 0 100%;
    text-align: center; }
  .newsletter-form.side {
    transition: right ease-in-out 500ms;
    background-color: #eee;
    border: 1px solid #333;
    border-right: 0;
    border-radius: 20px 0 0 20px;
    padding: 20px 40px;
    position: fixed;
    top: 10px;
    bottom: 10px;
    right: 0;
    min-width: 320px;
    max-width: 500px; }
  .newsletter-form button {
    display: inline-block;
    background-color: #f00;
    text-transform: uppercase;
    color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
    font-size: 19.2px;
    text-align: center;
    border-radius: 20px;
    border: none;
    max-width: 500px;
    width: 100%;
    padding: 8px;
    cursor: pointer;
    transition: all ease-in-out 200ms; }
    .newsletter-form button:hover {
      letter-spacing: 1px; }
  .newsletter-form input {
    background-color: #fff;
    border: none;
    transition: all ease-in-out 200ms; }
    .newsletter-form input:focus {
      outline: none;
      box-shadow: 0 0 0 2px #FF0000; }
  .newsletter-form.hide {
    right: -100%; }
  .newsletter-form .input-field + .input-field {
    margin-top: 40px; }
  .newsletter-form .input-field label {
    display: block;
    font-size: 16px;
    font-weight: 600;
    padding-left: 19.2px;
    margin: 5px 0 0; }
  .newsletter-form .input-field input {
    display: block;
    border-radius: 20px;
    padding: 10px;
    border: 1px solid #333;
    width: 100%;
    margin-top: 8px; }
  .newsletter-form .interests h4 {
    margin: 0;
    text-transform: uppercase;
    font-size: 19.2px;
    letter-spacing: 1px; }
  .newsletter-form .interests .checkboxes {
    display: flex;
    flex-wrap: wrap; }
    .newsletter-form .interests .checkboxes + h4 {
      margin-top: 40px; }
  .newsletter-form .interests .checkbox-field {
    width: 50%;
    margin: 5px 0; }
    .newsletter-form .interests .checkbox-field label {
      cursor: pointer;
      font-size: 16px;
      text-transform: uppercase;
      position: relative;
      transition: all ease-in-out 200ms; }
      .newsletter-form .interests .checkbox-field label::before {
        content: '';
        margin-right: 10px;
        display: inline-block;
        vertical-align: text-top;
        width: 20px;
        height: 20px;
        background: #fff;
        transition: all ease-in-out 200ms; }
      .newsletter-form .interests .checkbox-field label::after {
        transition: all ease-in-out 200ms; }
    .newsletter-form .interests .checkbox-field input {
      position: absolute;
      opacity: 0; }
      .newsletter-form .interests .checkbox-field input:hover + label::before {
        background-color: #FF0000; }
      .newsletter-form .interests .checkbox-field input:focus + label::before {
        box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12); }
      .newsletter-form .interests .checkbox-field input:checked + label::before {
        background: #FF0000; }
      .newsletter-form .interests .checkbox-field input:disabled + label {
        color: #b8b8b8;
        cursor: auto; }
      .newsletter-form .interests .checkbox-field input:disabled + label:before {
        box-shadow: none;
        background: #ddd; }
      .newsletter-form .interests .checkbox-field input:checked + label:after {
        content: '';
        position: absolute;
        left: 5px;
        top: 9px;
        background: #fff;
        width: 2px;
        height: 2px;
        box-shadow: 2px 0 0 #fff, 4px 0 0 #fff, 4px -2px 0 #fff, 4px -4px 0 #fff, 4px -6px 0 #fff, 4px -8px 0 #fff;
        transform: rotate(45deg); }
